import React, { Component } from 'react';
import 'react-widgets/dist/css/react-widgets.css';
import {
  Button,
  Card,
  CardBody,
  CardHeader,
  Col,
  Container,
  Form,
  FormGroup,
  Input,
  Label,
  Row,
} from 'reactstrap';
import axios from 'axios';
import {BootstrapTable, TableHeaderColumn,TableDataColumn} from 'react-bootstrap-table';
import {Combobox} from 'react-widgets';

class Forms extends Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.toggleFade = this.toggleFade.bind(this);
    this.state = {
      collapse: true,
      fadeIn: true,
      timeout: 300,
      companies:[],
      places:[],placeId:'',
      unloadingPlace:'',unloadingRate:'',company:[],
      btnText:'Save',btnColor:'dark'
    };
    this.onChange = this.onChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }

  componentDidMount(){
      this. getCompanies();
      this.getUnloadingPlaces();
  }

  onChange(e) {
    this.setState({ [e.target.name]: e.target.value })
  }

 onSubmit(e){
    e.preventDefault();
    if(!this.state.btnText.localeCompare('Save')){
      this.addLorry();
    }
    else{
      this.updateLorry();
    }
    
  }

  toggle() {
    this.setState({ collapse: !this.state.collapse });
  }

  toggleFade() {
    this.setState((prevState) => { return { fadeIn: !prevState }});
  }
  //fetching companies from companies
  getCompanies(){
    fetch('/getcompanies')
    .then(res => res.json())
    .then(json =>{
      this.setState({ 
        companies : json
      });
    });
  }
  //adding a new unloading Place to database
  addUnloadingPlace= async()=>{
    await axios.post('/addplace',{
        unloadingPlace: this.state.unloadingPlace,
        unloadingRate: this.state.unloadingRate,
        company:this.state.company.companyId
    }).then(res =>{
      var status=res.data.status;
      if(!status.localeCompare('SuccessfullyAdded')){
        alert('Successfully Added');
        this.getUnloadingPlaces();
      }
      else{
        alert('Bad Credentials')
      }
    })
  }
//Update a unloading Place
updateUnloadingPlace= async()=>{
  await axios.post('/updateplace',{
      unloadingPlace: this.state.unloadingPlace,
      unloadingRate: this.state.unloadingRate,
      company:this.state.placeId
  }).then(res =>{
    var status=res.data.status;
    if(!status.localeCompare('SuccessfullyAdded')){
      alert('Successfully Updated');
      this.getUnloadingPlaces();
    }
    else{
      alert('Bad Credentials')
    }
  })
}
  //fetch lorries from database
  getUnloadingPlaces(){
    fetch('/getunloadingplaces')
    .then(res => res.json())
    .then(json =>{
      this.setState({ 
       places : json
      });
    });
  }
//event handlers for lorry updates and deletes
  onRowSelect=(rowKeys)=>{
this.setState({
  unloadingPlace:rowKeys.uploadingPlaceAddress,
  unloadingRate:rowKeys.unloadingPlaceIncomeRate,
  placeId:rowKeys.uploadingPlaceId,
  btnText:'Update',
  btnColor:'info'
})
  }

 onAfterDeleteRow=(rowKeys)=> {
  axios.post('/deleteplace',{
    company:rowKeys
  }).then(res =>{
    var status=res.data.status;
    if(!status.localeCompare('SuccessfullyAdded')){
      alert('Successfully Deleted');
      this.getUnloadingPlaces();
    }
    else{
      alert('Failed To Delete')
    }
  })
    
  }
  
  options = {
    afterDeleteRow: this.onAfterDeleteRow  // A hook for after droping rows.
  };
  
  selectRowProp = {
    mode: 'radio',
    onSelect: this.onRowSelect,
    bgColor:'yellow'
  };

  //event handler for clear button
  clearForm=()=>{

  }
  //event handler for save button
  handleSave=()=>{
    if(!this.state.btnText.localeCompare('Save')){
      this.addUnloadingPlace();
    }
    else{
      this.updateUnloadingPlace();
    }
  }
  render() {
    return (
      <div className="animated fadeIn">
         <Container>
        <Row>
          <Col xs="12" sm="6">
            <Card>
              <CardHeader>
                <strong>Unloading Place</strong>
              </CardHeader>
              <CardBody>
                <Form onSubmit={this.onSubmit}>
              <FormGroup row>
                  </FormGroup>
                <FormGroup>
                    <Label>Company</Label>
                    <Col xs="12" md="9">
                    <div>
                      <Combobox
                        data={this.state.companies}
                        suggest={true}
                        //valueField='companyName'
                        textField='companyName'
                        caseSensitive={false}
                        minLength={2}
                        filter='contains'
                        onChange={(value)=>{
                          this.setState({
                            company:value
                          }
                        );
                        }
                        }
                      />
                  </div>
                    </Col>
                </FormGroup>
                <FormGroup>
                  <Label htmlFor="place">Unloading Place</Label>
                  <Input type="text" name="unloadingPlace"   value={this.state.unloadingPlace} onChange={this.onChange} />
                </FormGroup>
                <FormGroup>
                  <Label htmlFor="rate">Unloading Rate</Label>
                  <Input type="text" name="unloadingRate" value={this.state.unloadingRate} onChange={this.onChange}/>
                </FormGroup>
                  <FormGroup row className="my-0">
                  <Col xs="6">
                    <FormGroup>
                      <Button block color={this.state.btnColor} onClick={this. handleSave}>{this.state.btnText}</Button>
                    </FormGroup>
                  </Col>
                  <Col xs="6">
                    <FormGroup>
                      <Button block color="dark" onClick={this.clearForm}>clear</Button>
                    </FormGroup>
                  </Col>
                  </FormGroup>
                  </Form>
              </CardBody>
            </Card>
          </Col>
          <Col  xs="6" ys="6" lg="6" xl="6">
          <Card>
              <CardBody style={{backgroundColor:"#d9d9d9"}}>
          <div>
            <BootstrapTable data={this.state.places} height='310' scrollTop={'Bottom'} deleteRow={ true } selectRow={this.selectRowProp} options={this.options}
            search={true}
            tableStyle={ { border: '#0000FF 2.5px solid' } }
            containerStyle={ { border: '#FFBB73 2.5px solid' } }
            headerStyle={ { border: 'red 1px solid' } }
            bodyStyle={ { border: 'green 1px solid' }}>
              <TableHeaderColumn width="60" dataField="uploadingPlaceId" isKey>No</TableHeaderColumn>
              <TableHeaderColumn width="110" dataField="companyName">Company</TableHeaderColumn>
              <TableHeaderColumn width="140" dataField="uploadingPlaceAddress">Unloading Place</TableHeaderColumn>
              <TableHeaderColumn width="110" dataField="unloadingPlaceIncomeRate">Unloading Rate</TableHeaderColumn>
            </BootstrapTable>
            </div>
            </CardBody>
            </Card>
    
          </Col>
        
        </Row>  
        </Container>
       
        
      </div>
    );
  }
}

export default Forms;
